import React, { Fragment } from 'react';
import gql from 'graphql-tag';
import { Query } from 'react-apollo';
import { Link } from 'react-router-dom';
import Moment from 'react-moment';

const LAUNCH_QUERY = gql`
    query LaunchQuery($flight_number: Int!) {
        launch(flight_number: $flight_number) {
            flight_number,
            mission_name,
            launch_year,
            launch_success,
            launch_date_utc,
            rocket {
                rocket_id,
                rocket_name,
                rocket_type
            }
        }
    }
`;

const Launch = (props) => {

    let { flight_number } = props.match.params;
    flight_number = parseInt(flight_number);

    return (
        <Fragment>
            <Query query={LAUNCH_QUERY} variables={{flight_number}}>
                {
                    ({ loading, error, data }) => {
                        if(loading) return <h4>Loading...</h4>;
                        if(error) console.log(error);

                        const { flight_number, mission_name, launch_year, launch_success, launch_date_utc, rocket: { 
                            rocket_id, rocket_name, rocket_type }} = data.launch;
                        
                        let launchSuccess;
                        if (launch_success === true) {
                            launchSuccess = <span className='text-success'>Yes!</span>;
                        } else if (launch_success === false) {
                            launchSuccess = <span className='text-danger'>No</span>;
                        } else {
                            launchSuccess = <span className='text-warning'>Unknown</span>;
                        }

                        return (
                        <div>
                            <h1 className='display-4 my-3'><span className='text-dark'>Mission:</span> {mission_name}</h1>
                            <h4 className='mb-3'>Launch Details</h4>
                            <ul className='list-group'>
                                <li className='list-group-item'>Flight Number: {flight_number}</li>
                                <li className='list-group-item'>Launch Year: {launch_year}</li>
                                <li className='list-group-item'>Launch Time: <Moment format='YYYY-MM-DD HH:mm'>{launch_date_utc}</Moment> UTC</li>
                                <li className='list-group-item'>Launch Successful: {launchSuccess}</li>
                            </ul>
                            <h4 className='my-3'>Rocket Details</h4>
                            <ul className='list-group'>
                                <li className='list-group-item'>Rocket ID: {rocket_id}</li>
                                <li className='list-group-item'>Rocket Name: {rocket_name}</li>
                                <li className='list-group-item'>Rocket Type: {rocket_type}</li>
                            </ul>
                            <hr/>
                            <Link to='/' className='btn btn-secondary'>Back</Link>
                        </div>
                        );
                    }
                }
            </Query>
        </Fragment>
    );
}

export default Launch;